<template>
  <scroll-view class="max_width">
    <main-title :thisPage=thisPage @hideSearchBox='hideSearchBox' @toSearch='toSearch' :hideSearch=hideSearch></main-title>
    <div class="contentBox">
      <div class="listBox" v-if="listShow" :class="{listHide:listHide}">
        <div :key=key v-for="(x,key) in dataList">
          <index-card :animation='animation' :goods=x @toDetail='toDetail' :index='key' :leftNone='leftNone'></index-card>
        </div>
      </div>
      <div class="searchBox" v-if="listHide">
        <search-box :searchPage=thisPage :searchNew=searchNew></search-box>
      </div>
    </div>
  </scroll-view>
</template>

<script>
import mainTitle from "@/components/mainTitle";
import indexCard from "@/components/indexCard";
import searchBox from "@/components/searchBox";

export default {
  data() {
    return {
      thisPage: "index",
      prePage: undefined,
      leftNone: false,
      dataList: [],
      listShow: true,
      listHide: false,
      hideSearch: false,
      searchNew: 0,
      animation: true,
      title: "大疆飞手百科"
    };
  },
  components: {
    mainTitle,
    indexCard,
    searchBox
  },
  methods: {
    hideSearchBox() {
      console.log(123);
      wx.setStorageSync("search_page", false);

      this.hideSearch = false;
      this.listHide = false;
      this.listShow = true;
    },
    showIndex() {
      var data = wx.getStorageSync("index");
      console.log(data);
      if (!data) {
        return;
      }
      this.dataList = data.data.data;
      if (this.prePage == "none") {
        this.animation = false;
      }
      wx.setStorageSync("pre_page", "none");
      wx.setNavigationBarTitle({
        title: this.title
      });
      this.getList();
    },
    get_user() {
      wx.setStorageSync("data_box", [{ pre_page: "begin" }]);
      wx.setStorageSync("pre_page", "begin");
      var _this = this;
      wx.login({
        success: function(res) {
          if (res.code) {
            console.log(res);
            var Fly = require("flyio/dist/npm/wx");
            var fly = new Fly();
            fly
              .post(`${wx.getStorageSync("url")}/wechatLogin`, {
                code: res.code
              })
              .then(d => {
                //输出请求数据
                console.log("req", d.data);
                wx.setStorageSync("YX-SESSIONID", d.data.data.sessionId);

                _this.start_index();
              })
              .catch(err => {
                console.log(err.status, err.message);
              });
          } else {
            console.log("登录失败！" + res.errMsg);
          }
        }
      });
    },
    start_index() {
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .get(`${wx.getStorageSync("url")}/`, {})
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
          wx.setStorageSync("index", {
            pre_page: this.thisPage,
            data: d.data,
            page: "index"
          });
          // wx.setStorageSync("pre_page", this.thisPage);
          this.showIndex();
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    toSearch() {
      this.hideSearch = true;
      this.listHide = true;
      setTimeout(() => {
        this.listShow = false;
        this.searchNew = +new Date();
      }, 500);
    },
    toDetail(x) {
      var Fly = require("flyio/dist/npm/wx");
      var fly = new Fly();
      var header = wx.getStorageSync("YX-SESSIONID");
      fly.interceptors.request.use(request => {
        request.headers["YX-SESSIONID"] = header;
        return request;
      });
      fly
        .get(`${wx.getStorageSync("url")}/type/${x.id}`, {})
        .then(d => {
          //输出请求数据
          console.log("req", d.data);
          wx.setStorage({
            key: "goods",
            data: JSON.stringify(x)
          });
          wx.setStorageSync("pre_page", this.thisPage);

          var url;
          if (d.data.data.type == "series") {
            wx.setStorageSync("childIndex", {
              title: x.title,
              data: d.data
            });

            url = "../childIndex/main";
            wx.setStorageSync("share_childIndex", x);
          } else {
            wx.setStorageSync("explain", {
              title: x.title,
              data: d.data
            });
            url = "../explain/main";
            wx.setStorageSync("share_explain", x);
                  wx.setStorageSync("share_player_list", x.id);

          }
          wx.navigateTo({ url });
        })
        .catch(err => {
          console.log(err.status, err.message);
        });
    },
    getList() {
      this.leftNone = false;
      setTimeout(() => {
        this.leftNone = true;
      }, 200);
    },
    exit() {
      // var SP = wx.getStorageSync("search_page");
      // console.log(SP);
      // if (SP) {
      //   wx.setStorageSync("search_page", false);
      //   return;
      // }
      this.leftNone = false;
      this.listShow = true;
      this.listHide = false;
      this.dataList = [];
      this.hideSearch = false;
      this.animation = true;

      this._watchers = [];
    }
  },

  onShow() {
    wx.showShareMenu({
      withShareTicket: false,
      success: () => {},
      fail: () => {},
      complete: () => {}
    });

    // 线上
    wx.setStorageSync("url", "https://feishou-baike.djiits.com");
    wx.setStorageSync("media_url", "https://feishou-baike.djicdn.com");

    // // 测试
    // wx.setStorageSync("url", "https://dj.majiangyun.com");
    // wx.setStorageSync("media_url",'https://bj-crm-wechat-mini-program-test.s3.cn-north-1.amazonaws.com.cn')

    wx.setStorageSync("search_page", "none");
    this.prePage = wx.getStorageSync("pre_page");
    if (this.prePage == "none") {
      this.animation = false;
    } else {
      this.exit();
    }

    if (this.prePage == "none") {
      console.log("back");
      this.showIndex();
    } else {
      console.log("start");
      this.get_user();
    }
  },
  onHide() {
    this.exit();
  },
  onUnload() {
    this.exit();
  },
  onShareAppMessage: function() {
    console.log(this.title);
    return {
      title: this.title,
      path: "/pages/index/main"
    };
  },
  onLoad() {
    var mta = require("../../utils/mta_analysis.js");
    mta.App.init({
      appID: "500649139",
      // eventID: "500015824", // 高级功能-自定义事件统计ID，配置开通后在初始化处填写
      lauchOpts: {}, //渠道分析,需在onLaunch方法传入options,如onLaunch:function(options){...}
      statPullDownFresh: true, // 使用分析-下拉刷新次数/人数，必须先开通自定义事件，并配置了合法的eventID
      statShareApp: true, // 使用分析-分享次数/人数，必须先开通自定义事件，并配置了合法的eventID
      statReachBottom: true // 使用分析-页面触底次数/人数，必须先开通自定义事件，并配置了合法的eventID
    });
    mta.Page.init();
  }
};
</script>


<style scoped lang='scss'>
.listBox {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  transition-timing-function: ease-in;
  -webkit-transform: translate3d(0, 0, 0); /*开启硬件加速*/
  transform: translateY(0);
  transition: transform 500ms;
  // position: absolute;
  top: 0;
  z-index: 200;
  width: 100%;
}
.searchBox {
  // position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
}
.listHide {
  transform: translateY(100%);
}
.contentBox {
  position: relative;
}
</style>



